/* eslint-disable react/prop-types */
import React, { Component } from "react";

import classes from "./AdminPanel.module.css";
import {
  SERVER_USER_WORKSPACES,
  LOCAL_SERVER_URL,
  SERVER_WORKSPACE_INVITE_USER,
} from "../../../common/Url";
import Layout from "../../../components/Layout/Layout";
import Footer from "../../../components/Footer/Footer";
import Member from "../../../components/Member/Member";
import Modal from "../../../components/Modal/Modal";
import { UserRoles } from "../../../common/UserRoles";
import { checkStorageToken } from "../../../common/TokenUtil";

import {
  Grid,
  Button,
  TextField,
  MenuItem,
  Typography,
} from "@material-ui/core";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";

import axios from "axios";
import { connect } from "react-redux";
import * as actionTypes from "../../../store/actions";
import { FormattedMessage, injectIntl } from "react-intl";

class AdminPanel extends Component {
  constructor(props) {
    super(props);
    checkStorageToken();
    if (!props.match.params.id) {
      window.location.href = LOCAL_SERVER_URL;
    }

    this.state = {
      newNewWorkspace: {
        name: "",
        description: "",
        role: "",
      },
      workspaceId: props.match.params.id,
      addNewMemeber: false,
      newMemeber: {
        role: "",
        email: "",
      },
      memberList: [{ role: "", email: "", name: "" }],
      modalAlertMessage: "",
    };

    this.closeAddMemberModal = this.closeAddMemberModal.bind(this);
    this.handleRoleChange = this.handleRoleChange.bind(this);
    this.updateNewMember = this.updateNewMember.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  componentDidMount() {
    const url =
      SERVER_USER_WORKSPACES + "/" + this.state.workspaceId + "/members";

    const config = {
      headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
    };

    axios
      .get(url, config)
      .then((response) => response.data)
      .then((data) => {
        let members = [];
        for (let i = 0; i < data.length; i++) {
          let user = data[i];
          members[i] = { role: user.role, email: user.email, name: user.name };
        }
        this.setState({ memberList: members });
      });
  }

  showAddMemberModal() {
    this.setState({ addNewMemeber: true });
  }

  closeAddMemberModal() {
    this.setState({ addNewMemeber: false });
  }

  updateNewMember() {
    let postData = {
      role: "",
      email: "",
      workspaceId: this.state.workspaceId,
    };

    postData.email = this.state.newMemeber.email;
    postData.role = this.state.newMemeber.role;
    const config = {
      headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
    };

    axios
      .post(SERVER_WORKSPACE_INVITE_USER, postData, config)
      .then((res) => {
        console.log(res);
        this.setState({
          addNewMemeber: false,
          memberList: [...this.state.memberList, this.state.newMemeber],
        });
      })
      .catch((err) => {
        let alertMessage = "";
        if (err.response) {
          if (err.response.status == 409) {
            alertMessage = "The user has existed in this workspace";
          } else if (err.response.status == 404) {
            alertMessage = "Cannot find the user";
          } else {
            alertMessage = "Error code: " + err.response.status;
          }
        } else {
          alertMessage = "Cannot add the member";
        }
        this.setState({ modalAlertMessage: alertMessage });
      });
  }

  handleChange(event) {
    const value = event.target.value;
    this.setState({
      ...this.state,
      [event.target.name]: value,
      newMemeber: {
        role: this.state.newMemeber.role,
        email: event.target.value,
      },
    });
  }

  handleRoleChange(role) {
    this.setState({
      newMemeber: {
        role: role,
        email: this.state.newMemeber.email,
      },
    });
  }

  render() {
    return (
      <Layout
        rolename={this.props.intl.formatMessage({
          id: "toolbar.admin",
        })}
      >
        <Grid
          style={{
            marginTop: 80,
            marginLeft: 100,
            marginRight: 100,
          }}
          item
          xs={12}
        >
          {/* title area */}
          <div className={classes.TitleAndSearch}>
            <div className={classes.Title}>
              <FormattedMessage id="admin.team"></FormattedMessage>
            </div>
          </div>

          {/* button and search area */}
          <Grid
            container
            direction="row"
            justify="space-between"
            spacing={0}
            item
            xs={12}
          >
            <Grid
              style={{
                marginTop: 20,
                marginBottom: 20,
              }}
              item
              xs={4}
            >
              <Button
                variant="contained"
                startIcon={<AccountCircleIcon />}
                type="submit"
                value="Submit"
                onClick={() => {
                  this.showAddMemberModal();
                }}
              >
                <FormattedMessage id="admin.invite"></FormattedMessage>
              </Button>
            </Grid>
            <Grid
              style={{
                marginTop: 20,
                marginBottom: 20,
              }}
              item
              xs={6}
            >
              <Typography
                gutterBottom
                variant="h6"
                style={{
                  color: "#edeede",
                }}
              >
                Workspace ID: {this.state.workspaceId}
              </Typography>
            </Grid>
          </Grid>

          {/* members lists area */}
          <Grid
            container
            direction="row"
            justify="space-evenly"
            spacing={0}
            style={{
              height: 700,
              background: "#5D5D5B",
              borderRadius: 10,
              color: "#5D5D5B",
            }}
            item
            xs={12}
          >
            {/* admin members area */}
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.MemberTitle}>
                <FormattedMessage id="admin.admin"></FormattedMessage>
              </div>
              {this.state.memberList.map((item) => {
                if (item.role === UserRoles.ADMIN)
                  return <Member key={item.email} membername={item.email} />;
              })}
            </Grid>
            {/* designer members area */}
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.MemberTitle}>
                <FormattedMessage id="admin.designer"></FormattedMessage>
              </div>
              {this.state.memberList.map((item) => {
                if (item.role === UserRoles.DESIGNER)
                  return <Member key={item.email} membername={item.email} />;
              })}
            </Grid>
            {/* developer members area */}
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                marginRight: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.MemberTitle}>
                <FormattedMessage id="admin.developer"></FormattedMessage>
              </div>
              {this.state.memberList.map((item) => {
                if (item.role === UserRoles.DEVELOPER)
                  return <Member key={item.email} membername={item.email} />;
              })}
            </Grid>
          </Grid>
        </Grid>
        <Modal
          show={this.state.addNewMemeber}
          modalClosed={this.closeAddMemberModal}
        >
          <form className={classes.form} noValidate>
            <div className={classes.ModalTitle}>
              <FormattedMessage id="admin.memberInfo"></FormattedMessage>
            </div>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  id="email"
                  label={this.props.intl.formatMessage({
                    id: "admin.email",
                  })}
                  name="email"
                  value={this.state.newMemeber.email}
                  onChange={this.handleChange}
                  autoComplete="email"
                />
              </Grid>
            </Grid>
            <Grid item xs={12}>
              <TextField
                select
                fullWidth
                label={this.props.intl.formatMessage({
                  id: "admin.role",
                })}
                required
                helperText={this.props.intl.formatMessage({
                  id: "admin.selectRole",
                })}
                style={{ marginTop: 10 }}
              >
                <MenuItem
                  value={this.props.intl.formatMessage({
                    id: "admin.admin",
                  })}
                  onClick={() => this.handleRoleChange(UserRoles.ADMIN)}
                >
                  <FormattedMessage id="admin.admin"></FormattedMessage>
                </MenuItem>
                <MenuItem
                  value={this.props.intl.formatMessage({
                    id: "admin.designer",
                  })}
                  onClick={() => this.handleRoleChange(UserRoles.DESIGNER)}
                >
                  <FormattedMessage id="admin.designer"></FormattedMessage>
                </MenuItem>
                <MenuItem
                  value={this.props.intl.formatMessage({
                    id: "admin.developer",
                  })}
                  onClick={() => this.handleRoleChange(UserRoles.DEVELOPER)}
                >
                  <FormattedMessage id="admin.developer"></FormattedMessage>
                </MenuItem>
              </TextField>
            </Grid>
            <Button
              fullWidth
              variant="contained"
              color="primary"
              style={{
                marginTop: 10,
                marginBottom: 30,
              }}
              onClick={() => this.updateNewMember()}
            >
              <FormattedMessage id="admin.inviteButton"></FormattedMessage>
            </Button>
            <Grid item>
              <p style={{ color: "red" }}>{this.state.modalAlertMessage}</p>
            </Grid>
          </form>
        </Modal>
        <Footer />
      </Layout>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    currWorkspaces: state.allNewWorkspaces,
    filter: state.filter,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateCurrentWorkspace: (currentWorkspace) =>
      dispatch({
        type: actionTypes.UPDATE_CURRENT_WORKSPACE,
        data: currentWorkspace,
      }),
    updateAllWorkspaces: (allNewWorkspaces) =>
      dispatch({
        type: actionTypes.UPDATE_ALL_NEWWORKSPACES,
        data: allNewWorkspaces,
      }),
  };
};

export default injectIntl(
  connect(mapStateToProps, mapDispatchToProps)(AdminPanel)
);
