/* eslint-disable react/prop-types */
import React, { Component } from "react";

import classes from "./Workspaces.module.css";
import Workspace from "../../components/Workspace/WorkspaceBar";
import Layout from "../../components/Layout/Layout";
import Footer from "../../components/Footer/Footer";
import {
  SERVER_USER_WORKSPACES,
  ADMIN_URL,
  DEVELOPER_ALLTASKS_URL,
  DESIGNER_URL,
} from "../../common/Url";
import { checkStorageToken } from "../../common/TokenUtil";
import { UserRoles } from "../../common/UserRoles";
import Modal from "../../components/Modal/Modal";

import {
  TextField,
  Grid,
  IconButton,
  InputAdornment,
  Button,
} from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import AddCircleIcon from "@material-ui/icons/AddCircle";

import axios from "axios";
import { connect } from "react-redux";
import * as actionTypes from "../../store/actions";
import { FormattedMessage, injectIntl } from "react-intl";

class Workspaces extends Component {
  constructor(props) {
    super(props);

    checkStorageToken();

    this.state = {
      currWorkspaces: [],
      filter: "",
      addNewWorkspace: false,
      newNewWorkspace: {
        name: "",
        description: "",
        role: "",
      },
      curList: [{ id: "", name: "", description: "", role: "" }],
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleDescriptionChange = this.handleDescriptionChange.bind(this);
    this.closeAddNewWorkspaceModal = this.closeAddNewWorkspaceModal.bind(this);
    this.updateNewWorkspace = this.updateNewWorkspace.bind(this);
  }

  componentDidMount() {
    const config = {
      headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
    };

    axios
      .get(SERVER_USER_WORKSPACES, config)
      .then((response) => response.data)
      .then((data) => {
        this.props.updateAllWorkspaces(data);
      });
  }

  /**
   * Handling the input name changes
   * @param {*} event
   */
  handleChange(event) {
    const value = event.target.value;
    this.setState({
      ...this.state,
      [event.target.name]: value,
      newNewWorkspace: {
        name: event.target.value,
        description: this.state.newNewWorkspace.description,
      },
    });
  }

  /**
   * Handling the input description changes
   * @param {*} event
   */
  handleDescriptionChange(event) {
    const value = event.target.value;
    this.setState({
      ...this.state,
      [event.target.name]: value,
      newNewWorkspace: {
        description: event.target.value,
        name: this.state.newNewWorkspace.name,
        // TODO: change to get role from backend later
        role: UserRoles.ADMIN,
      },
    });
  }

  /**
   * Updating the newNewWorkspace in state
   */
  updateNewWorkspace() {
    const workspaceData = {
      name: this.state.newNewWorkspace.name,
      description: this.state.newNewWorkspace.description,
    };

    const config = {
      headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
    };

    axios
      .post(SERVER_USER_WORKSPACES, workspaceData, config)
      .then((res) => {
        console.log(res);
        if (res.data) {
          workspaceData.id = res.data;
          this.setState({ newNewWorkspace: workspaceData });
          console.log(workspaceData);

          this.setState({
            addNewWorkspace: false,
            curList: [...this.state.curList, this.state.newNewWorkspace],
          });
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }

  /**
   * Handling when clicking the Enter button
   * update the current workspace
   * and redirect to admin, developer or designer page accordingly
   * @param {*} data
   */
  directToWorkspace(data) {
    this.props.updateCurrentWorkspace(data);

    let directToURL = "";
    if (data.role === UserRoles.ADMIN) {
      directToURL = ADMIN_URL + "/" + data.id;
    } else if (data.role === UserRoles.DESIGNER) {
      directToURL = DESIGNER_URL + "/" + data.id;
    } else if (data.role === UserRoles.DEVELOPER) {
      directToURL = DEVELOPER_ALLTASKS_URL;
    }

    this.props.history.push({
      pathname: directToURL,
      data,
    });
  }

  /**
   * Show the modal for adding new workspace
   */
  showAddNewWorkspaceModal() {
    this.setState({ addNewWorkspace: true });
  }

  /**
   * Close the modal for adding new workspace
   */
  closeAddNewWorkspaceModal() {
    this.setState({ addNewWorkspace: false });
  }

  render() {
    return (
      <Layout
        rolename={this.props.intl.formatMessage({
          id: "toolbar.workspaces",
        })}
      >
        <Grid
          style={{
            marginTop: 80,
            marginLeft: 100,
            marginRight: 100,
          }}
          item
          xs={12}
        >
          {/* title area */}
          <div className={classes.TitleAndSearch}>
            <div className={classes.Title}>
              <FormattedMessage id="workspaces.allWorkspaces"></FormattedMessage>
            </div>
          </div>

          {/* button and search area */}
          <Grid
            container
            direction="row"
            justify="space-between"
            spacing={0}
            item
            xs={12}
          >
            <Grid
              style={{
                marginTop: 20,
                marginBottom: 20,
              }}
              item
              xs={4}
            >
              <Button
                variant="contained"
                startIcon={<AddCircleIcon />}
                type="submit"
                value="Submit"
                onClick={() => {
                  this.showAddNewWorkspaceModal();
                }}
              >
                <FormattedMessage id="workspaces.create"></FormattedMessage>
              </Button>
            </Grid>
            <Grid
              style={{
                marginTop: 20,
                marginBottom: 20,
              }}
              item
              xs={6}
            >
              <TextField
                style={{
                  display: "flex",
                  borderColor: "white",
                  border: 0,
                  borderRadius: 3,
                  backgroundColor: "white",
                  paddingLeft: 10,
                  paddingRight: 10,
                }}
                placeholder={this.props.intl.formatMessage({
                  id: "workspaces.search",
                })}
                variant="standard"
                name="filter"
                value={this.state.filter}
                onChange={this.handleChange}
                InputProps={{
                  startAdornment: (
                    <InputAdornment>
                      <IconButton>
                        <SearchIcon />
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
              />
            </Grid>
          </Grid>

          {/* workspaces area */}
          <Grid
            container
            direction="row"
            justify="space-evenly"
            spacing={0}
            style={{
              height: 700,
              background: "#5D5D5B",
              borderRadius: 10,
              color: "#5D5D5B",
            }}
            item
            xs={12}
          >
            {/* admin workspace area */}
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.WorkspaceTitle}>
                <FormattedMessage id="workspaces.admin.workspaces"></FormattedMessage>
              </div>
              {this.props.currWorkspaces.map((item) => {
                if (
                  item.role === UserRoles.ADMIN &&
                  item.name
                    .toLowerCase()
                    .includes(this.state.filter.toLowerCase())
                )
                  return (
                    <Workspace
                      key={item.id}
                      workspacename={item.name}
                      workspacedescription={item.description}
                      clicked={() => this.directToWorkspace(item)}
                    />
                  );
              })}
            </Grid>
            {/* designer workspaces area */}
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.WorkspaceTitle}>
                <FormattedMessage id="workspaces.designer.workspaces"></FormattedMessage>
              </div>
              {this.props.currWorkspaces.map((item) => {
                if (
                  item.role === UserRoles.DESIGNER &&
                  item.name
                    .toLowerCase()
                    .includes(this.state.filter.toLowerCase())
                )
                  return (
                    <Workspace
                      key={item.id}
                      workspacename={item.name}
                      workspacedescription={item.description}
                      clicked={() => this.directToWorkspace(item)}
                    />
                  );
              })}
            </Grid>
            {/* developer workspaces area */}
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                marginRight: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.WorkspaceTitle}>
                <FormattedMessage id="workspaces.developer.workspaces"></FormattedMessage>
              </div>
              {this.props.currWorkspaces.map((item) => {
                if (
                  item.role === UserRoles.DEVELOPER &&
                  item.name
                    .toLowerCase()
                    .includes(this.state.filter.toLowerCase())
                )
                  return (
                    <Workspace
                      key={item.id}
                      workspacename={item.name}
                      workspacedescription={item.description}
                      clicked={() => this.directToWorkspace(item)}
                    />
                  );
              })}
            </Grid>
          </Grid>
        </Grid>

        {/* create workspace modal */}
        <Modal
          show={this.state.addNewWorkspace}
          modalClosed={this.closeAddNewWorkspaceModal}
        >
          <form className={classes.form} noValidate>
            <div className={classes.ModalTitle}>
              <FormattedMessage id="workspaces.workspaceInfo"></FormattedMessage>
            </div>
            {/* name field */}
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  id="name"
                  label={this.props.intl.formatMessage({
                    id: "workspaces.name",
                  })}
                  name="name"
                  value={this.state.newNewWorkspace.name}
                  onChange={this.handleChange}
                />
              </Grid>
            </Grid>
            {/* description field */}
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  id="description"
                  label={this.props.intl.formatMessage({
                    id: "workspaces.description",
                  })}
                  name="description"
                  value={this.state.newNewWorkspace.description}
                  onChange={this.handleDescriptionChange}
                />
              </Grid>
            </Grid>
            {/* create button field */}
            <Button
              fullWidth
              variant="contained"
              color="primary"
              style={{
                marginTop: 10,
                marginBottom: 30,
              }}
              onClick={() => this.updateNewWorkspace()}
            >
              <FormattedMessage id="workspaces.createButton"></FormattedMessage>
            </Button>
          </form>
        </Modal>
        {/* footer */}
        <Footer />
      </Layout>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    currWorkspaces: state.allNewWorkspaces,
    filter: state.filter,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateCurrentWorkspace: (currentWorkspace) =>
      dispatch({
        type: actionTypes.UPDATE_CURRENT_WORKSPACE,
        data: currentWorkspace,
      }),
    updateAllWorkspaces: (allNewWorkspaces) =>
      dispatch({
        type: actionTypes.UPDATE_ALL_NEWWORKSPACES,
        data: allNewWorkspaces,
      }),
  };
};

export default injectIntl(
  connect(mapStateToProps, mapDispatchToProps)(Workspaces)
);
