/* eslint-disable react/prop-types */
import jwt from "jsonwebtoken";
import { SIGN_IN_URL } from "./Url";
const secret =
  "A4Tc6Qa5Cr<6xt{eZ/3AC93|B5iF]0;G0vH+AT=g*{5/_93)q,*M[T8Jbispbd5vb6S-4TTC";

/**
 * Check whether the storage token exist or expired
 * return the alert
 */
export function checkStorageToken() {
  let token = localStorage.getItem("token");

  if (!token) {
    window.location.href = SIGN_IN_URL;
    alert("You have to sign in first!");
  }

  jwt.verify(token, secret, function (err) {
    if (err) {
      alert("The account state is expired, please sign in again.");
      window.localStorage.removeItem("token");
      window.localStorage.removeItem("email");
      window.location.href = SIGN_IN_URL;
    }
  });
}

/**
 * Decode the email in the token
 * set the email in the localStorage
 */
export function decodeStorageToken(token) {
  jwt.verify(token, secret, function (err, decoded) {
    if (decoded.email) {
      localStorage.setItem("email", decoded.email);
    }
  });
}
