/* eslint-disable react/prop-types */
import React, { Component } from "react";
import { connect } from "react-redux";
import detectBrowserLanguage from "detect-browser-language";

import classes from "./Toolbar.module.css";
import UserMenu from "../UserMenu";
import * as actionTypes from "../../../store/actions";
import {
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Typography,
} from "@material-ui/core";
import { FormattedMessage, injectIntl } from "react-intl";

// eslint-disable-next-line no-unused-vars
class Toolbar extends Component {
  constructor(props) {
    super(props);
    props.updateCurrentLanguage(detectBrowserLanguage());
  }

  changeLan(lan) {
    this.props.updateCurrentLanguage(lan);
  }

  render() {
    // check if token exist, get the user email then render the menu accordingly
    const checkStorageToken = localStorage.getItem("token");
    const userEmail = localStorage.getItem("email");

    return (
      <header className={classes.Toolbar}>
        <Typography variant="h5" color="inherit">
          <img
            src="https://mortiesbaobao.s3-us-west-1.amazonaws.com/WATERFLOW-logo.png"
            alt="logo"
            height="30"
            width="35"
            display="inline-block"
          />
          {this.props.rolename}
        </Typography>

        <div>
          {/* hello user form */}
          {checkStorageToken && (
            <FormControl
              className={classes.formControl}
              style={{ marginRight: 40 }}
            >
              <Typography variant="subtitle1" color="inherit">
                <FormattedMessage id="toolbar.hello"></FormattedMessage>
                {userEmail}
              </Typography>
            </FormControl>
          )}

          {/* Language selection form */}
          <FormControl
            className={classes.formControl}
            style={{ marginRight: 20 }}
          >
            <InputLabel
              shrink
              style={{
                display: "inline",
                position: "absolute",
              }}
              htmlFor="name-multiple"
            >
              <FormattedMessage id="toolbar.language"></FormattedMessage>
            </InputLabel>
            <Select labelId="demo-simple-select-label" id="demo-simple-select">
              <MenuItem onClick={() => this.changeLan("zh")} value={"zh"}>
                简体中文
              </MenuItem>
              <MenuItem onClick={() => this.changeLan("zh_tw")} value={"zh_tw"}>
                繁體中文
              </MenuItem>
              <MenuItem onClick={() => this.changeLan("jp")} value={"jp"}>
                日本語
              </MenuItem>
              <MenuItem onClick={() => this.changeLan("en")} value={"en"}>
                English
              </MenuItem>
            </Select>
          </FormControl>
          {/* User menu form */}
          <FormControl className={classes.formControl}>
            <UserMenu />
          </FormControl>
        </div>
      </header>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    curLan: state.currentLanguage,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateCurrentLanguage: (currentLan) =>
      dispatch({ type: actionTypes.UPDATE_LANGUAGE, data: currentLan }),
  };
};

export default injectIntl(
  connect(mapStateToProps, mapDispatchToProps)(Toolbar)
);
