import React from "react";
import { Link } from "react-router-dom";

import { WORKSPACES_URL, SIGN_UP_URL, SIGN_IN_URL } from "../../common/Url";

import IconButton from "@material-ui/core/IconButton";
import Menu from "@material-ui/core/Menu";
import MenuIcon from "@material-ui/icons/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import FaceIcon from "@material-ui/icons/Face";
import LowPriorityIcon from "@material-ui/icons/LowPriority";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
import AssignmentIcon from "@material-ui/icons/Assignment";
import { FormattedMessage } from "react-intl";

export default function UserMenu() {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleLogOut = () => {
    localStorage.removeItem("token");
    localStorage.removeItem("email");
  };

  const checkStorageToken = localStorage.getItem("token");

  // check if token exist, render the menu accordingly
  if (checkStorageToken) {
    return (
      <div>
        <IconButton
          aria-controls="simple-menu"
          aria-haspopup="true"
          onClick={handleClick}
        >
          <MenuIcon />
        </IconButton>
        <Menu
          id="simple-menu"
          anchorEl={anchorEl}
          keepMounted
          open={Boolean(anchorEl)}
          onClose={handleClose}
        >
          <MenuItem onClick={handleClose}>
            <ListItemIcon>
              <FaceIcon fontSize="default" />
            </ListItemIcon>
            <FormattedMessage id="toolbar.profile"></FormattedMessage>
          </MenuItem>
          <MenuItem component={Link} to={WORKSPACES_URL} onClick={handleClose}>
            <ListItemIcon>
              <LowPriorityIcon fontSize="default" />
            </ListItemIcon>
            <FormattedMessage id="toolbar.back"></FormattedMessage>
          </MenuItem>
          <MenuItem component={Link} to={"/"} onClick={handleLogOut}>
            <ListItemIcon>
              <ExitToAppIcon fontSize="default" />
            </ListItemIcon>
            <FormattedMessage id="toolbar.logout"></FormattedMessage>
          </MenuItem>
        </Menu>
      </div>
    );
  }
  return (
    <div>
      <IconButton
        aria-controls="simple-menu"
        aria-haspopup="true"
        onClick={handleClick}
      >
        <MenuIcon />
      </IconButton>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem component={Link} to={SIGN_UP_URL} onClick={handleClose}>
          <ListItemIcon>
            <AssignmentIcon fontSize="default" />
          </ListItemIcon>
          <FormattedMessage id="landing.signUp"></FormattedMessage>
        </MenuItem>
        <MenuItem component={Link} to={SIGN_IN_URL} onClick={handleClose}>
          <ListItemIcon>
            <AccountCircleIcon fontSize="default" />
          </ListItemIcon>
          <FormattedMessage id="landing.signIn"></FormattedMessage>
        </MenuItem>
      </Menu>
    </div>
  );
}
